import React from 'react';
import { Link } from 'react-router-dom';
import MapComponent from './MapComponent';

class Home extends React.Component {
    componentDidMount(){
        const target = document.querySelector('.home-maps');
        const target2 = document.querySelector('.home-title');
        const target3 = document.querySelector('.home-sections');
        let observer = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                if(entry.isIntersecting){
                    entry.target.style.opacity = 1;
                }
            })
        }, {
            root: null,
            rootMargin: "0px",
            threshold: 0.1
        });
        observer.observe(target);
        observer.observe(target2);
        observer.observe(target3);
    }
    render() {
        return (
            <div className='home-page'>
                <div className='home-title'>
                    <h1>Bienvenido a lentes-colombia.com</h1>
                </div>
                <div className='home-sections'>
                    <Link className='home-section' to='/lentesdecontacto/1'>
                        <div className='home-section-content'>
                            <h4>Lentes de contacto</h4>
                        </div>
                    </Link>
                    <Link className='home-section' to='/soluciones/1' >
                        <div className='home-section-content'>
                            <h4>Soluciones</h4>
                        </div>
                    </Link>
                    <Link className='home-section' to='/gotashidratantes/1' >
                        <div className='home-section-content'>
                            <h4>Gotas hidratantes</h4>
                        </div>
                    </Link>
                    <Link className='home-section' to='cuidadoybelleza/1' >
                        <div className='home-section-content'>
                            <h4>Cuidado y belleza</h4>
                        </div>
                    </Link>
                </div>
                <div className='home-maps'>
                    <div className='home-map'>
                        <div className='home-map-title'>
                            <h2>Visitanos <br /> (cra 22 #19 - 40)</h2>
                        </div>
                        <MapComponent center={[4.6149994,-74.0850269]} markerCenter={[4.6149994,-74.0850269]} zoom={17} />
                    </div>
                </div>
            </div>
        )
    }
}

export default Home;